.header {
    border-bottom: 1px solid #707070;
    padding: vw(22px) vw(90px) vw(18px) vw(20px);
    text-align: center;
    color: #000000;
    font-size: vw(25px);
    font-weight: 500;
    line-height: vw(55px);
}

@media (max-width: $table) {
    .header {
        display: none;
    }
}
